.todo-item {
    margin: 10px;
    font-size: 20px;
}

.todo-del-btn {
    width: 50px;
    height: 30px;
    background-color: rgb(233, 79, 92);
    border-radius: 5px;
    color: white;
    border: none;
    font-size: 15px;
    float: right;
    cursor: pointer;
}

.todo-item > span {
    margin-left: 10px;
}

.todo-item > li {
    height: 30px;
}
